import React from 'react'
import {useState,useEffect} from 'react'
import {Container,Title,CopyCom} from '../common'
import {injectIntl,FormattedMessage} from 'gatsby-plugin-intl'
import { navigate } from 'gatsby'
import config from '../../libs/config'
import contractAbi from '../../libs/abi'
import Cookies from 'js-cookie'
import Web3 from 'web3'
import Skeleton from '../skeleton'
import {PulseLoader,BeatLoader,BounceLoader} from 'react-spinners'
import {toast} from 'react-toastify'
import BigNumber from 'bignumber.js'
const web4 = new Web3(config.provider);
const contract = new web4.eth.Contract(contractAbi,config.contractAddress);
const reg = /^[0-9]+$/;
const SignUp = () => {
    const [loading,setLoading] = useState(true);
    const [refIdAddress,setRefIdAddress] = useState(null);
    const [getUp,setGetUp] = useState(false);
    const [userInputId,setUserInputId] = useState('');
    const [check,setCheck] = useState(false);
    const [auto,setAuto] = useState(true);
    const [send,setSend] = useState(false);
    useEffect(()=>{
        const a = async () => {
            let id;
            id = Cookies.get('ref');
            console.log(id);
            if (!id || !reg.test(id)){
                return setLoading(false);
            }
            const refAddress = await contract.methods.uL(id).call();
            if (refAddress === '0x0000000000000000000000000000000000000000'){
                return setLoading(false);
            }
            setRefIdAddress(refAddress);
            setLoading(false);
        }
        a();
    },[null])
    const handleGetUpline = async () => {
        try {
            setGetUp(true);
            let address = config.owner;
            const events = await contract.getPastEvents('rLE',{fromBlock: 0});
            if (events.length > 0){
                address = events[events.length-1].returnValues._u;
            }
            console.log(address);
            setRefIdAddress(address);
            setGetUp(false);
        } catch (err){
        }
    }
    const Check = async () =>{
        if (web4.utils.isAddress(userInputId)){
            setCheck(true);
            const refAddress = await contract.methods.us(userInputId).call();
            if (refAddress.x){
                setCheck(false);
                setRefIdAddress(userInputId);
            } else {
                setCheck(false);
                toast('Ethereum address not found in contract.');
            }
        }
        if (reg.test(userInputId)){
            setCheck(true);
            const refAddress = await contract.methods.uL(userInputId).call();
            if (refAddress === '0x0000000000000000000000000000000000000000'){
                setCheck(false);
                return toast('Id not found in contract.');
            }
            setCheck(false)
            setRefIdAddress(refAddress);
        }
    }
    const handleSignUp = async()=>{
        if (window.ethereum && !send){
            setSend(true)
            window.web3 = new Web3(window.ethereum,null,{transactionConfirmationBlocks: 1});
            try {
                // const netVersion = await window.web3.eth.net.getId();
                // if (netVersion !== config.net){
                //     toast.error(config.noti)
                //     setSend(false);
                //     return;
                // }
                const acc = await window.ethereum.enable();
                const checkUs = await contract.methods.us(acc[0]).call();
                if (checkUs.x){
                    setSend(false);
                    return toast.error('This address is already registered. Please Login.')
                }
                const gasPrice = await web4.eth.getGasPrice();
                const bigNumberGwei = new BigNumber(gasPrice).plus(config.gweiPlus).toString();
                // const estPrice = await web4.eth.estimateGas({from: acc[0],to: config.contractAddress,value: web4.utils.toWei(`${config.prices[0]}`,'ether'),data: refIdAddress,gas: 1000000,gasPrice});
                // console.log(estPrice);
                let gasCal = await window.web3.eth.estimateGas({from: acc[0],to: config.contractAddress,value: web4.utils.toWei(`${config.prices[0]}`,'ether'),data: refIdAddress});
                const bigNumberGasCal = new BigNumber(gasCal).plus('1');
                console.log(gasCal.toString());
                await window.web3.eth.sendTransaction({from: acc[0],to: config.contractAddress,value: web4.utils.toWei(`${config.prices[0]}`,'ether'),data: refIdAddress,gas: bigNumberGasCal,gasPrice: bigNumberGwei});
                toast.success('Success.')
                Cookies.set('user',acc[0]);
                navigate('/db/dashboard');
                setSend(false);
            } catch(error){
                alert(error);
                setSend(false);
            }
        } else {
            setSend(false);
        }
    }
    if (loading){
        return (
            <div className="lg-350 mx-auto">
                <Title>
                    <span className="text-lights-orange">Sign </span>
                    Up
                </Title>
                <div className="font-sans font-semibold text-14 text-lights-bluebold mb-8 text-center">
                    <Skeleton />
                </div>
                <div className="mb-4 text-center">
                    <Skeleton height={40}/>
                </div>
                <div className="text-center mb-4 text-lights-bluebold font-sans font-semibold">
                    <Skeleton width={25} height={25} circle={true} />
                </div>
                <div className="mb-4">
                    <div className="font-sans font-semibold italic text-14 text-lights-bluebold mb-2 leading-none">
                        <Skeleton />
                    </div>
                    <Skeleton height={40}/>
                </div>
                <div className="text-center">
                    <Skeleton height={37} width={160}/>
                </div>
            </div>
        )
    }
    if (!loading && !refIdAddress){
        return (
        <div className="lg-350 mx-auto">
            <Title>
                <span className="text-lights-orange">Sign </span>
                Up
            </Title>
            <div className="font-sans font-semibold text-14 text-lights-bluebold mb-8 text-center">
                <FormattedMessage id="auth.block1.never" defaultMessage="We never ask you to enter your private keys." />
            </div>
            <div className="mb-4 text-center">
                <button className="w-full py-2 text-lights-white font-semibold focus:outline-none bg-lights-bluebold rounded-lg uppercase flex justify-center items-center hover:bg-lights-orangebold" onClick={handleGetUpline}>
                    {!getUp ?
                     <span className="mx-auto"><FormattedMessage id="auth.block1.signbtn1" defaultMessage="Auto Get Your Upline" /></span>:
                        <span className="mx-auto"><BounceLoader
                        sizeUnit={"px"}
                        size={24}
                        color={'#fff'}
                        /></span>
                    }
                </button>
            </div>
            <div className="text-center mb-4 text-lights-bluebold font-sans font-semibold">
                <FormattedMessage id="auth.block1.or" defaultMessage="or"/>
            </div>
            <div className="mb-4">
                <div className="font-sans font-semibold italic text-14 text-lights-bluebold mb-2 leading-none">
                    <FormattedMessage id="auth.block1.signenter" defaultMessage="Enter your id or Ethereum address of your upline" />
                </div>
                <input type="text" className="block w-full px-2 py-2 focus:outline-none border rounded-lg text-lights-bluebold text-14 font-sans font-semibold" name="userInput" id="userInput" value={userInputId} onChange={(e)=>setUserInputId(e.target.value)}/>
            </div>
            <div className="text-center">
                <button className="mx-auto flex items-center justify-center px-4 py-2 bg-lights-orange text-lights-bluebold text-14 font-semibold rounded-lg focus:outline-none uppercase" onClick={Check} style={{width: '170px'}}>
                    {!check ?
                    <span className="mx-auto"><FormattedMessage id="auth.block1.signget" defaultMessage="Check Your Upline"/></span>:
                    <span className="mx-auto"><BounceLoader
                        sizeUnit={"px"}
                        size={21}
                        color={'#fff'}
                        height={24}
                    /></span>
                    }
                </button>
            </div>
        </div>
    )
    }
    return (
        <>
            <Title><span className="text-lights-orange">Sign</span> Up</Title>
            <div className="mb-8 text-center">
                <button className={`px-2 py-2 border border-r-0 border-lights-bluebold rounded-l focus:outline-none hover:bg-lights-bluebold hover:text-white ${auto ? 'bg-lights-bluebold text-white':''}`} onClick={()=>setAuto(true)}>Automatically</button>
                <button className={`px-2 py-2 border rounded-r focus:outline-none border-lights-bluebold hover:bg-lights-bluebold hover:text-white ${auto ? '':'bg-lights-bluebold text-white'}`} onClick={()=>setAuto(false)}>Manually</button>
            </div>
            {
                auto ? (
                    <>
                        <div className="mb-8">
                            <button className="w-full py-2 bg-lights-bluebold focus:outline-none uppercase text-white font-sans font-semibold rounded-lg hover:bg-lights-orange flex items-center justify-center" onClick={handleSignUp}>
                                {!send ? <span className="mx-auto"><FormattedMessage id="auth.block1.oneclick" defaultMessage="Sign up per 1 click"/></span>:
                                    <span className="mx-auto"><BounceLoader color={'#fff'} size={24}/></span>
                                }
                            </button>
                        </div>
                        <div className="w-full h-px bg-lights-grayblack mb-8"></div>
                        <div className="font-sans font-semibold text-14 text-lights-bluebold text-center">
                            <FormattedMessage id="auth.block1.note1" defaultMessage="Pay attention, to make automaticall registration on the mobile device you have to enter to the app, to choose DApp (browser) menu and to enter referral link in the address bar. Then in the same built-in decentralized browser go to the registration page, and make a registration in 1 click."/>
                        </div>
                    </>
                ):(
                    <>
                        <div className="font-sans font-semibold text-14 text-lights-bluebold mb-8 text-center">
                            <FormattedMessage id="auth.block1.sign1" defaultMessage="Send FROM YOUR Ethereum wallet {amount} ETH to the smart contract address. In the field Input DATA enter Ethereum address of your upline." values={{amount: 0.1}} />
                        </div>
                        <div className="mb-4">
                            <div className="font-sans font-semibold italic text-14 text-lights-bluebold mb-2 leading-none">
                                <FormattedMessage id="auth.block1.sign2" defaultMessage="Send to (Smart contract address)" />
                            </div>
                            <CopyCom text={config.contractAddress}/>
                        </div>
                        <div className="mb-4">
                            <div className="font-sans font-semibold italic text-14 text-lights-bluebold mb-2 leading-none">
                                <FormattedMessage id="auth.block1.sign3" defaultMessage="The Amount in ETH" />
                            </div>
                            <CopyCom text={config.prices[0]}/>
                        </div>
                        <div className="mb-4">
                            <div className="font-sans font-semibold italic text-14 text-lights-bluebold mb-2 leading-none">
                                <FormattedMessage id="auth.block1.sign4" defaultMessage="Input DATA (Your upline address)" />
                            </div>
                            <CopyCom text={refIdAddress} />
                        </div>
                        <div className="font-sans font-semibold text-14 text-lights-bluebold text-center">
                            <FormattedMessage id="auth.block1.sign5" defaultMessage="After a successful transaction, registration (1 level purchase) will be automatically completed, and it will be possible to LOG IN to the System."/>
                        </div>
                    </>
                )
            }
        </>
    )
}
const Block1 = ({location}) => {
    return (
        <Container>
            <div className="md:flex lg:w-4/5 mx-auto">
                <div className="md:w-1/2 md:px-4 mx-auto">
                    <SignUp />                    
                </div>
            </div>
        </Container>
    )
}
export default injectIntl(Block1)